div#project {
    height: 100vh;
    padding: 15vh 5vw;
    display: flex;
    flex-direction: column;
}

.accordion {
    color: #444;
    background-color: #eee;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 6px;
    border: none;
    outline: none;
}

.accordion:hover {
    background-color: #ccc;
}

.panel-accordion.opened {
    padding: 10px 25px;
    animation: panel-toggler 0.75s;
    height: 100%;
}

.panel-accordion {
    height: 0px;
    overflow: hidden;
}

@keyframes panel-toggler {
    0% {height: 0px;}
    100% {height: 100%;}
}

@media screen and (min-width:600px) {
    div#project {
        display: none;
    }
}