* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins';
}

.header {
    display: flex;
    flex-direction: column;
    text-align: center;
    top: 0;
    left: 0;
    width: 100vw;
    position: fixed;
    z-index: 1;
    box-shadow: 0 0 10px 5px #292929;
}

header > .fa {
    height: 5vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    color: #50cced;
}

header .navbar a {
    display: none;
    transition: height 0.5s;
}

header .navbar.responsive a {
    height: 7vh;
    text-decoration: none;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #fff;
}

header .navbar.responsive a:last-of-type {
    border: none;
}

section {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.greeting > p, .greeting > h1 {
    font-size: 26px;
}

#profil-img {
    width: 65vw;
    border-radius: 117.5px;
    /* margin-bottom: 25px; */
}

#btn-journey {
    height: 36px;
    width: 140px;
    border-radius: 25px;
}

#quote {
    width: 80vw;
    height: 20vw;
    margin-top: 15px;
    border-top: 1px solid gray;
    border-bottom: 1px solid gray;
}

.adios {
    width: 60vw;
}

.kutipan > h5 {
    font-size: 36px;
    margin-bottom: 10px;
}

#seneca-img {
    width: 50vw;
    border-radius: 25px;
}

.tentang {
    text-align: justify;
    padding: 50px;
}

.tentang > h2 {
    font-size: 22px;
}

.tentang > p {
    margin: 25px 0;
}

.tentang .fa {
    rotate: 45deg;
}

.kontak > h5 {
    font-size: 20px;
}

.kontak p {
    opacity: 0.5;
}

#social-media .fa {
    background-color: #292929;
    color: #fff;
    font-size: 36px;
    padding: 8px;
    margin: 6px;
    border-radius: 8px;
}

footer {
    display: none;
}

@media screen and (min-width: 600px) {
    .header, section {
        display: none;
    }

    #footer {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: 100vh;
        background-color: #000;
    }
    
    #footer > h2 {
        font-size: 36px;
    }

    #footer > p {
        font-size: 18px;
    }

    #footer > img {
        width: 25vw;
    }
}