.square {
    font-style: 'Poppins';
    width: 40px;
    height: 40px;
    border-radius: 5px;
    margin: 5px;
    font-size: 26px;
    text-align: center;
    text-transform: uppercase;
}

.alert-block {
    position: absolute;
    height: 50vh;
    width: 75vw;
    border-radius: 15px;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.alert-block .title {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.title .fa {
    font-size: 24px;
}

#congratulation {
    font-size: 24px;
}

#kata-rahasia {
    text-transform: uppercase;
}