html {
    scroll-behavior: smooth;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    /* border: 1px solid gray; */
}

.bg-white {
    background-color: #fff;
}

.bg-black {
    background-color: #292929;
}

.bg-blue {
    background-color: #50cced;
}

.cl-white {
    color: #fff;
}

.cl-blue {
    color: #50cced;
}

.cl-black {
    color: #292929;
}

.container {
    margin: 45px;
    padding: 15px;
}

.mt-5 {
    margin-top: 10px;
}

.vh-100 {
    height: 100vh;
}

.vh-100

.d-flex {
    display: flex;
}

.d-flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.column {
    flex-direction: column;
}

.char {
    margin: 10px;
    width: 50px;
    height: 50px;
    border-radius: 10px;
    font-size: 24px;
    padding: 5%;
}

.btn {
    width: 90px;
    height: 40px;
    border-radius: 15px;
    font-size: 14px;
    font-weight: bolder;
}

.btn:hover {
    cursor: pointer;
}

.btn-green {
    background-color: #198754;
    color: #fff;
}

.btn-green:hover {
    background-color: #fff;
    color: #198754;
}

